@keyframes border-flicker {
  0% {
    // opacity: 0.1;

    filter: drop-shadow(0 0 0.1rem #5ac5d9);
  }
  2% {
    // opacity: 1;
    filter: drop-shadow(0 0 1rem #5ac5d9);
  }
  4% {
    // opacity: 0.1;
    filter: drop-shadow(0 0 0.1rem #5ac5d9);
  }

  8% {
    // opacity: 1;
    filter: drop-shadow(0 0 1rem #5ac5d9);
  }
  70% {
    // opacity: 0.7;
    filter: drop-shadow(0 0 0.35rem #5ac5d9);
  }
  100% {
    // opacity: 1;
    filter: drop-shadow(0 0 1rem #5ac5d9);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(1);
  }
  30% {
    transform: scale(1.2);
  }
  40% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes moveInTop {
  0% {
    opacity: 0;
    transform: translateY(15%);
  }
  90% {
    transform: translateY(-0.5rem);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(20%);
  }
  90% {
    transform: translateX(-0.5rem);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(-20%);
  }
  90% {
    transform: translateX(1rem);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
